'use client';

import { useState } from 'react';
import { useFindMany, useDeleteOne, useCreateOne, useUpdateOne } from '@/hooks/useDynamicGraphQL';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { 
  Plus, 
  Search, 
  Edit, 
  Trash2,
  Folder,
  AlertCircle,
  BookOpen,
  FolderOpen
} from 'lucide-react';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { useToast } from '@/hooks/use-toast';

interface Category {
  id: string;
  name: string;
  slug: string;
  description?: string;
  _count?: {
    courses: number;
  };
}

export default function AdminCategoriesPage() {
  const { toast } = useToast();
  const [searchQuery, setSearchQuery] = useState('');
  const [dialogOpen, setDialogOpen] = useState(false);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [editMode, setEditMode] = useState(false);
  const [categoryToDelete, setCategoryToDelete] = useState<Category | null>(null);
  const [formData, setFormData] = useState({
    id: '',
    name: '',
    slug: '',
    description: '',
  });

  const { data: categories, loading, error, refetch } = useFindMany('CourseCategory', {
    select: {
      id: true,
      name: true,
      slug: true,
      description: true,
    },
    include: {
      _count: {
        select: {
          courses: true,
        },
      },
    },
    orderBy: {
      name: 'asc',
    },
  });

  const [createCategory, { loading: createLoading }] = useCreateOne('CourseCategory');
  const [updateCategory, { loading: updateLoading }] = useUpdateOne('CourseCategory');
  const [deleteCategory, { loading: deleteLoading }] = useDeleteOne('CourseCategory');

  // Filter categories
  const filteredCategories = (categories || []).filter(category =>
    category.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    (category.description || '').toLowerCase().includes(searchQuery.toLowerCase())
  );

  // Handlers
  const handleCreate = () => {
    setEditMode(false);
    setFormData({ id: '', name: '', slug: '', description: '' });
    setDialogOpen(true);
  };

  const handleEdit = (category: Category) => {
    setEditMode(true);
    setFormData({
      id: category.id,
      name: category.name,
      slug: category.slug,
      description: category.description || '',
    });
    setDialogOpen(true);
  };

  const handleDeleteClick = (category: Category) => {
    setCategoryToDelete(category);
    setDeleteDialogOpen(true);
  };

  const generateSlug = (name: string) => {
    return name
      .toLowerCase()
      .normalize('NFD')
      .replace(/[\u0300-\u036f]/g, '')
      .replace(/ƒë/g, 'd')
      .replace(/[^a-z0-9\s-]/g, '')
      .replace(/\s+/g, '-')
      .replace(/-+/g, '-')
      .trim();
  };

  const handleNameChange = (name: string) => {
    setFormData({
      ...formData,
      name,
      slug: generateSlug(name),
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.name.trim()) {
      toast({
        title: 'L·ªói',
        description: 'Vui l√≤ng nh·∫≠p t√™n danh m·ª•c',
        type: 'error',
      });
      return;
    }

    try {
      if (editMode) {
        await updateCategory({
          where: { id: formData.id },
          data: {
            name: formData.name,
            slug: formData.slug,
            description: formData.description || null,
          },
        });

        toast({
          title: 'Th√†nh c√¥ng',
          description: 'ƒê√£ c·∫≠p nh·∫≠t danh m·ª•c',
          type: 'success',
        });
      } else {
        await createCategory({
          data: {
            name: formData.name,
            slug: formData.slug,
            description: formData.description || null,
          },
        });

        toast({
          title: 'Th√†nh c√¥ng',
          description: 'ƒê√£ t·∫°o danh m·ª•c m·ªõi',
          type: 'success',
        });
      }

      setDialogOpen(false);
      setFormData({ id: '', name: '', slug: '', description: '' });
      refetch();
    } catch (error: any) {
      toast({
        title: 'L·ªói',
        description: error.message || 'Kh√¥ng th·ªÉ l∆∞u danh m·ª•c',
        type: 'error',
      });
    }
  };

  const handleDeleteConfirm = async () => {
    if (!categoryToDelete) return;

    try {
      await deleteCategory({
        where: { id: categoryToDelete.id },
      });

      toast({
        title: 'Th√†nh c√¥ng',
        description: `ƒê√£ x√≥a danh m·ª•c "${categoryToDelete.name}"`,
        type: 'success',
      });

      setDeleteDialogOpen(false);
      setCategoryToDelete(null);
      refetch();
    } catch (error: any) {
      toast({
        title: 'L·ªói',
        description: error.message || 'Kh√¥ng th·ªÉ x√≥a danh m·ª•c',
        type: 'error',
      });
    }
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8 space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div>
          <h1 className="text-2xl sm:text-3xl font-bold text-gray-900">Qu·∫£n l√Ω danh m·ª•c</h1>
          <p className="text-sm sm:text-base text-gray-600 mt-1">T·ªïng c·ªông {categories?.length || 0} danh m·ª•c</p>
        </div>
        <Button onClick={handleCreate} className="gap-2 w-full sm:w-auto">
          <Plus className="w-4 h-4" />
          T·∫°o danh m·ª•c m·ªõi
        </Button>
      </div>

      {/* Search */}
      <div className="relative">
        <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
        <Input
          placeholder="T√¨m ki·∫øm danh m·ª•c..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="pl-10"
        />
      </div>

      {/* Categories Grid */}
      {loading ? (
        <div className="text-center py-12">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="text-gray-500 mt-4">ƒêang t·∫£i...</p>
        </div>
      ) : error ? (
        <Card>
          <CardContent className="py-12 text-center">
            <AlertCircle className="w-12 h-12 text-red-500 mx-auto mb-4" />
            <p className="text-red-600">L·ªói: {error.message}</p>
          </CardContent>
        </Card>
      ) : filteredCategories.length === 0 ? (
        <Card>
          <CardContent className="py-12 text-center">
            <FolderOpen className="w-12 h-12 text-gray-400 mx-auto mb-4" />
            <p className="text-gray-500 mb-4">Kh√¥ng t√¨m th·∫•y danh m·ª•c n√†o</p>
            <Button onClick={handleCreate}>
              T·∫°o danh m·ª•c ƒë·∫ßu ti√™n
            </Button>
          </CardContent>
        </Card>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
          {filteredCategories.map((category) => (
            <Card key={category.id} className="hover:shadow-lg transition-shadow">
              <CardHeader>
                <div className="flex items-start justify-between">
                  <div className="flex items-center gap-3">
                    <div className="p-2 bg-blue-100 rounded-lg">
                      <Folder className="w-5 h-5 text-blue-600" />
                    </div>
                    <div className="flex-1 min-w-0">
                      <CardTitle className="text-base truncate">{category.name}</CardTitle>
                      <p className="text-xs text-gray-500 truncate">/{category.slug}</p>
                    </div>
                  </div>
                </div>
              </CardHeader>
              <CardContent className="space-y-4">
                {/* Description */}
                {category.description && (
                  <CardDescription className="line-clamp-2 text-sm">
                    {category.description}
                  </CardDescription>
                )}

                {/* Stats */}
                <div className="flex items-center gap-2 text-sm text-gray-600">
                  <BookOpen className="w-4 h-4" />
                  <span>{category._count?.courses || 0} kh√≥a h·ªçc</span>
                </div>

                {/* Actions */}
                <div className="flex gap-2 pt-2 border-t">
                  <Button 
                    variant="outline" 
                    size="sm" 
                    className="flex-1 gap-2"
                    onClick={() => handleEdit(category)}
                  >
                    <Edit className="w-4 h-4" />
                    S·ª≠a
                  </Button>
                  <Button 
                    variant="outline" 
                    size="sm" 
                    className="gap-2 text-red-600 hover:text-red-700 hover:bg-red-50"
                    onClick={() => handleDeleteClick(category)}
                    disabled={(category._count?.courses || 0) > 0}
                  >
                    <Trash2 className="w-4 h-4" />
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}

      {/* Create/Edit Dialog */}
      <Dialog open={dialogOpen} onOpenChange={setDialogOpen}>
        <DialogContent className="sm:max-w-[500px] max-h-[90vh] flex flex-col">
          <DialogHeader>
            <DialogTitle>{editMode ? 'S·ª≠a danh m·ª•c' : 'T·∫°o danh m·ª•c m·ªõi'}</DialogTitle>
            <DialogDescription>
              {editMode ? 'C·∫≠p nh·∫≠t th√¥ng tin danh m·ª•c' : 'Nh·∫≠p th√¥ng tin danh m·ª•c m·ªõi'}
            </DialogDescription>
          </DialogHeader>
          
          <form onSubmit={handleSubmit} className="flex-1 overflow-y-auto">
            <div className="space-y-4 py-4">
              <div className="space-y-2">
                <Label htmlFor="name">T√™n danh m·ª•c *</Label>
                <Input
                  id="name"
                  placeholder="V√≠ d·ª•: L·∫≠p tr√¨nh Web"
                  value={formData.name}
                  onChange={(e) => handleNameChange(e.target.value)}
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="slug">Slug</Label>
                <Input
                  id="slug"
                  placeholder="lap-trinh-web"
                  value={formData.slug}
                  onChange={(e) => setFormData({ ...formData, slug: e.target.value })}
                  disabled
                  className="bg-gray-50"
                />
                <p className="text-xs text-gray-500">T·ª± ƒë·ªông t·∫°o t·ª´ t√™n danh m·ª•c</p>
              </div>

              <div className="space-y-2">
                <Label htmlFor="description">M√¥ t·∫£</Label>
                <Textarea
                  id="description"
                  placeholder="M√¥ t·∫£ chi ti·∫øt v·ªÅ danh m·ª•c..."
                  value={formData.description}
                  onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                  rows={4}
                />
              </div>
            </div>
          </form>

          <DialogFooter className="border-t pt-4">
            <Button
              type="button"
              variant="outline"
              onClick={() => setDialogOpen(false)}
              disabled={createLoading || updateLoading}
            >
              H·ªßy
            </Button>
            <Button
              type="submit"
              onClick={handleSubmit}
              disabled={createLoading || updateLoading}
            >
              {createLoading || updateLoading ? 'ƒêang l∆∞u...' : editMode ? 'C·∫≠p nh·∫≠t' : 'T·∫°o m·ªõi'}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Delete Confirmation Dialog */}
      <AlertDialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>X√°c nh·∫≠n x√≥a danh m·ª•c</AlertDialogTitle>
            <AlertDialogDescription>
              B·∫°n c√≥ ch·∫Øc ch·∫Øn mu·ªën x√≥a danh m·ª•c <strong>&quot;{categoryToDelete?.name}&quot;</strong>?
              <br />
              <br />
              {(categoryToDelete?._count?.courses || 0) > 0 ? (
                <span className="text-red-600 font-semibold">
                  Danh m·ª•c n√†y c√≥ {categoryToDelete?._count?.courses} kh√≥a h·ªçc. Kh√¥ng th·ªÉ x√≥a!
                </span>
              ) : (
                <span className="text-gray-600">
                  H√†nh ƒë·ªông n√†y kh√¥ng th·ªÉ ho√†n t√°c.
                </span>
              )}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>H·ªßy</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleDeleteConfirm}
              disabled={deleteLoading || (categoryToDelete?._count?.courses || 0) > 0}
              className="bg-red-600 hover:bg-red-700"
            >
              {deleteLoading ? 'ƒêang x√≥a...' : 'X√≥a danh m·ª•c'}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  );
}
